import {
	appUpdInfo
} from '../../../api/auth.js'
import {
	putUserInfo
} from '../../../setting/putUserInfo.js'
export default {
	data() {
		return {
			userInfo: null,
			chargeStatus: false,
			chargeMoney: 100
		}
	},
	onShow() {
		let userInfo = uni.getStorageSync('userInfo');
		this.userInfo = userInfo;
	},
	methods: {
		recharge() {
			// 充值
			let params = {
				money: Number(this.chargeMoney) + Number(this.userInfo.money)
			}
			appUpdInfo(params).then(data => {
				if (data.errno == 200) {
					uni.showToast({
						title: '充值成功'
					});
					putUserInfo(data).then(data => {
						let userInfo = uni.getStorageSync('userInfo');
						this.userInfo = userInfo;
						this.chargeStatus = false;
					}).catch(err => {})
				}
			}).catch(err => {})
		},
		coupon() {
			uni.showToast({
				icon: 'none',
				title: '暂未开放'
			})
		}
	}
}
